"use client";
import { Customizer, KeyboardLayout } from '@/components'
import { useKeyboardStyleApplier } from '@/hooks';

export default function Home() {
  const {
    color,
    setColor,
    image,
    setImage,
  } = useKeyboardStyleApplier();

  return (
    <div className="mt-6 flex justify-between w-10/12 mx-auto">
      <Customizer color={color} colorChange={setColor} image={image} imageChange={setImage} />
      <KeyboardLayout color={color} />
    </div>
  );
}
