import { useKeyboardStyleApplier } from "@/hooks";
import { Dispatch, SetStateAction } from "react";


type ColorCustomizer = {
  type: 'color';
  color?: string;
  fontColor?: string;
}

type ImageCustomizer = {
  type: 'image';
}

// type CustomizerProps = ColorCustomizer | ImageCustomizer


type CustomizerProps = {
  color: string;
  colorChange: Dispatch<SetStateAction<string>>;
  image: File | null;
  imageChange: Dispatch<SetStateAction<File | null>>;
};

export const Customizer = (
  {
    color,
    colorChange,
    image,
    imageChange,
  }: CustomizerProps
) => {
  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      console.log('Selected file:', file);
      imageChange(file);
    }
  };
  return <>
    <div className="flex flex-col gap-3">
      <div className="flex gap-3">
        <label htmlFor="color" id="color">Color: </label>

        <input type="color" id="color" value={color} onChange={el => colorChange(el.target.value)} />
      </div>

      <div className="flex gap-3">
        <label htmlFor="image" id="image-label" className="font-medium text-gray-700">
          Image:
        </label>

        <input
          type="file"
          id="image"
          onChange={handleImageChange}
        />
      </div>
    </div>

  </>

}
